.logo-argumentapp {
    width: 60px;
    margin: 10px 10px 10px 95px;
    transition: width, left, right, 0.3s;
}

.logo-argumentapp.collapsed {
    width: 40px;
    margin: 10px 20px 0 20px;
}

.menu-divider {
    width: 84%;
    margin: 25px 8% 25px 8% !important;
    border-color: #9c5fa2 !important;
    border-width: 1px !important;
    transition: width, left, right, 0.3s;
}

.menu-divider.collapsed {
    width: 60%;
    margin: 25px 20% 25px 20% !important;
}

.curva-superior {
    content: "";
    position: fixed;
    background-color: transparent;
    top: 20px;
    left: 270px;
    height: 37px;
    width: 70px;
    border-top-left-radius: 18px;
    box-shadow: -37px 0px 0 0 #fff;
    transition: width, left, right, 0.3s;
    z-index: 40;
}

.curva-inferior {
    content: "";
    position: fixed;
    background-color: transparent;
    bottom: 20px;
    left: 270px;
    height: 37px;
    width: 70px;
    border-bottom-left-radius: 18px;
    box-shadow: -37px 0px 0 0 #fff;
    transition: width, left, right, 0.3s;
    z-index: 40;
}

.caja-superior {
    content: "";
    position: fixed;
    background-color: #fff;
    top: 20px;
    left: 50px;
    height: 37px;
    width: 220px;
    transition: width, left, right, 0.3s;
    z-index: 40;
}

.caja-inferior {
    content: "";
    position: fixed;
    background-color: #fff;
    bottom: 20px;
    left: 50px;
    height: 37px;
    width: 220px;
    transition: width, left, right, 0.3s;
    z-index: 40;
}

.caja-superior.collapsed{
    width: 40px;
}

.caja-inferior.collapsed{
    width: 40px;
}

.curva-superior.collapsed {
    left: 100px;
}

.curva-inferior.collapsed {
    left: 100px;
}

.pro-sidebar {
    width: 250px;
    position: fixed;
    top: 50px;
    left: 20px;
    height: calc(100% - 100px);
}

.pro-sidebar > .pro-sidebar-inner {
    background: #fff;
    width: 250px;
}

.pro-sidebar > .pro-sidebar-inner > .pro-sidebar-layout .pro-sidebar-header {
    min-height: 100px;
    margin-top: 60px;
}

.pro-sidebar .pro-menu.shaped .pro-menu-item > .pro-inner-item > .pro-icon-wrapper {
    color: #9c5fa2;
}

.pro-sidebar .pro-menu.shaped .pro-menu-item.active > .pro-inner-item > .pro-icon-wrapper {
    color: #fff;
}

.pro-sidebar-content > nav > ul > li {
    transition: inherit;
    width: inherit;
    margin: 25px 0;
    height: 70px;  
    display: block;
    color: #9c5fa2;
    white-space: nowrap;
    overflow: hidden;
}

.pro-sidebar .pro-menu .pro-menu-item > .pro-inner-item {
    padding: 18px 20px 20px 10px;
}

.pro-sidebar .pro-menu .pro-menu-item > .pro-inner-item:hover {
    color: #9c5fa2;
}

.pro-sidebar .pro-menu .pro-menu-item.active {
    color: #fff;
    background: #9c5fa2;
    filter: drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.161));
    width: 264px;
    border-radius: 0px 18px 18px 0px;
    transition: width, left, right, 0.3s;
}

.pro-sidebar .pro-menu .pro-menu-item.active.collapsed {
    width: 94px;
}

.pro-sidebar .pro-menu .pro-menu-item > .pro-inner-item > .pro-item-content {
    font-size: 13px;
    font-weight: 600;
    margin-left: 0px;
    transition: width, left, right, 0.3s;
}

.pro-sidebar.collapsed .pro-menu .pro-menu-item > .pro-inner-item > .pro-item-content {
    color: transparent;
}

.pro-sidebar .pro-menu.shaped .pro-menu-item > .pro-inner-item > .pro-icon-wrapper {
    margin-left: 4px;
}

.pro-sidebar .pro-menu .pro-menu-item.active:hover {
    background: #6d4271;    
}

.pro-sidebar .pro-menu .pro-menu-item:hover {
    background: #fbf9fb;
}

.pro-sidebar .pro-menu .pro-menu-item.active > .pro-inner-item:hover {
    color: #fff;
}

.pro-sidebar > .pro-sidebar-inner > .pro-sidebar-layout {
    overflow-y: visible;
    overflow-x: visible;
}

.pro-sidebar .pro-menu.shaped .pro-menu-item > .pro-inner-item > .pro-icon-wrapper {
    background-color: transparent;
}

.pro-sidebar .pro-menu .pro-menu-item > .pro-inner-item > .pro-icon-wrapper {
    font-size: 18px;
}

.pro-sidebar > .pro-sidebar-inner > .pro-sidebar-layout .pro-sidebar-header {
    border-bottom: 0;
}