@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.8.2/font/bootstrap-icons.css");

body {
    height: 100%;
    min-height: 100vh;
    min-width: fit-content;
    width: 100%;
    background: linear-gradient(to right, #c7d9ff 0%, #d9e6ff 14.29%, #e6e9ff 32.02%, #f3eaff 47.29%, #f6efff 100%);
}

.page-wrap {
    position: fixed;
    top: 20px;
    right: 20px;
    left: 20px;
    bottom: 20px;
    background: white;
    border-radius: 18px;
    padding: 0px 30px 0px 30px;
    overflow-y: auto;
    overflow-x: hidden;
    /* overflow-y: calc(100% - 50px); */
  }

::-webkit-scrollbar-track {
    background: none;
}
::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}
::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 5px;
}

.login-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    min-height: 100vh !important;
    background: linear-gradient(to right, #cbdcff 0%, #d9e6ff 14.29%, #e6e9ff 32.02%, #f3eaff 47.29%, #f6efff 100%);
}

.argumentapp-center {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main-container {
    position: absolute;
    width: 100%;
    min-width: 500px;
    padding-top: 100px;
    padding-left: 160px;
    padding-right: 100px;
    background-color: #faf9fe !important;
    height: auto;
    min-height: 100%;
}
.main-container-sinMenu {
    position: absolute;
    width: 100%;
    padding-top: 100px;
    padding-left: 200px;
    padding-right: 100px;
    background-color: #faf9fe !important;
    min-height: 100%;
    left: 0;
}

.main-container .contenido {
    margin: 30px 30px 10px 150px;
}

.main-container .MuiTypography-titulo {
    margin-left: 150px;
}

.grupo-botones-config {
    display: flex;
    flex-direction: column;
}

*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  /* display: none; */
}
*::-webkit-scrollbar-track {
  background: #ddd;
  border-radius: 25px;
}

*::-webkit-scrollbar-thumb {
  background-color: #555;
  border-radius: 12px;
}