body {
    height: 100%;
    min-height: 100vh;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    width: 100%;
    background: linear-gradient(to right, #c7d9ff 0%, #d9e6ff 14.29%, #e6e9ff 32.02%, #f3eaff 47.29%, #f6efff 100%);
}

.page-wrap {
    position: fixed;
    top: 20px;
    right: 20px;
    left: 20px;
    bottom: 20px;
    background: white;
    border-radius: 18px;
    padding: 0px 30px 0px 30px;
    overflow-y: auto;
    overflow-x: hidden;
    /* overflow-y: calc(100% - 50px); */
  }

::-webkit-scrollbar-track {
    background: none;
}
::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}
::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 5px;
}

.login-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    min-height: 100vh !important;
    background: linear-gradient(to right, #cbdcff 0%, #d9e6ff 14.29%, #e6e9ff 32.02%, #f3eaff 47.29%, #f6efff 100%);
}

.argumentapp-center {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main-container {
    position: absolute;
    width: 100%;
    min-width: 500px;
    padding-top: 100px;
    padding-left: 160px;
    padding-right: 100px;
    background-color: #faf9fe !important;
    height: auto;
    min-height: 100%;
}
.main-container-sinMenu {
    position: absolute;
    width: 100%;
    padding-top: 100px;
    padding-left: 200px;
    padding-right: 100px;
    background-color: #faf9fe !important;
    min-height: 100%;
    left: 0;
}

.main-container .contenido {
    margin: 30px 30px 10px 150px;
}

.main-container .MuiTypography-titulo {
    margin-left: 150px;
}

.grupo-botones-config {
    display: flex;
    flex-direction: column;
}

*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  /* display: none; */
}
*::-webkit-scrollbar-track {
  background: #ddd;
  border-radius: 25px;
}

*::-webkit-scrollbar-thumb {
  background-color: #555;
  border-radius: 12px;
}
.logo-argumentapp {
    width: 60px;
    margin: 10px 10px 10px 95px;
    transition: width, left, right, 0.3s;
}

.logo-argumentapp.collapsed {
    width: 40px;
    margin: 10px 20px 0 20px;
}

.menu-divider {
    width: 84%;
    margin: 25px 8% 25px 8% !important;
    border-color: #9c5fa2 !important;
    border-width: 1px !important;
    transition: width, left, right, 0.3s;
}

.menu-divider.collapsed {
    width: 60%;
    margin: 25px 20% 25px 20% !important;
}

.curva-superior {
    content: "";
    position: fixed;
    background-color: transparent;
    top: 20px;
    left: 270px;
    height: 37px;
    width: 70px;
    border-top-left-radius: 18px;
    box-shadow: -37px 0px 0 0 #fff;
    transition: width, left, right, 0.3s;
    z-index: 40;
}

.curva-inferior {
    content: "";
    position: fixed;
    background-color: transparent;
    bottom: 20px;
    left: 270px;
    height: 37px;
    width: 70px;
    border-bottom-left-radius: 18px;
    box-shadow: -37px 0px 0 0 #fff;
    transition: width, left, right, 0.3s;
    z-index: 40;
}

.caja-superior {
    content: "";
    position: fixed;
    background-color: #fff;
    top: 20px;
    left: 50px;
    height: 37px;
    width: 220px;
    transition: width, left, right, 0.3s;
    z-index: 40;
}

.caja-inferior {
    content: "";
    position: fixed;
    background-color: #fff;
    bottom: 20px;
    left: 50px;
    height: 37px;
    width: 220px;
    transition: width, left, right, 0.3s;
    z-index: 40;
}

.caja-superior.collapsed{
    width: 40px;
}

.caja-inferior.collapsed{
    width: 40px;
}

.curva-superior.collapsed {
    left: 100px;
}

.curva-inferior.collapsed {
    left: 100px;
}

.pro-sidebar {
    width: 250px;
    position: fixed;
    top: 50px;
    left: 20px;
    height: calc(100% - 100px);
}

.pro-sidebar > .pro-sidebar-inner {
    background: #fff;
    width: 250px;
}

.pro-sidebar > .pro-sidebar-inner > .pro-sidebar-layout .pro-sidebar-header {
    min-height: 100px;
    margin-top: 60px;
}

.pro-sidebar .pro-menu.shaped .pro-menu-item > .pro-inner-item > .pro-icon-wrapper {
    color: #9c5fa2;
}

.pro-sidebar .pro-menu.shaped .pro-menu-item.active > .pro-inner-item > .pro-icon-wrapper {
    color: #fff;
}

.pro-sidebar-content > nav > ul > li {
    transition: inherit;
    width: inherit;
    margin: 25px 0;
    height: 70px;  
    display: block;
    color: #9c5fa2;
    white-space: nowrap;
    overflow: hidden;
}

.pro-sidebar .pro-menu .pro-menu-item > .pro-inner-item {
    padding: 18px 20px 20px 10px;
}

.pro-sidebar .pro-menu .pro-menu-item > .pro-inner-item:hover {
    color: #9c5fa2;
}

.pro-sidebar .pro-menu .pro-menu-item.active {
    color: #fff;
    background: #9c5fa2;
    -webkit-filter: drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.161));
            filter: drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.161));
    width: 264px;
    border-radius: 0px 18px 18px 0px;
    transition: width, left, right, 0.3s;
}

.pro-sidebar .pro-menu .pro-menu-item.active.collapsed {
    width: 94px;
}

.pro-sidebar .pro-menu .pro-menu-item > .pro-inner-item > .pro-item-content {
    font-size: 13px;
    font-weight: 600;
    margin-left: 0px;
    transition: width, left, right, 0.3s;
}

.pro-sidebar.collapsed .pro-menu .pro-menu-item > .pro-inner-item > .pro-item-content {
    color: transparent;
}

.pro-sidebar .pro-menu.shaped .pro-menu-item > .pro-inner-item > .pro-icon-wrapper {
    margin-left: 4px;
}

.pro-sidebar .pro-menu .pro-menu-item.active:hover {
    background: #6d4271;    
}

.pro-sidebar .pro-menu .pro-menu-item:hover {
    background: #fbf9fb;
}

.pro-sidebar .pro-menu .pro-menu-item.active > .pro-inner-item:hover {
    color: #fff;
}

.pro-sidebar > .pro-sidebar-inner > .pro-sidebar-layout {
    overflow-y: visible;
    overflow-x: visible;
}

.pro-sidebar .pro-menu.shaped .pro-menu-item > .pro-inner-item > .pro-icon-wrapper {
    background-color: transparent;
}

.pro-sidebar .pro-menu .pro-menu-item > .pro-inner-item > .pro-icon-wrapper {
    font-size: 18px;
}

.pro-sidebar > .pro-sidebar-inner > .pro-sidebar-layout .pro-sidebar-header {
    border-bottom: 0;
}
.menu-perfil-container {
    position: absolute;
    width: 350px;
    height: 310px;
    padding-bottom: 40px;
    right: 0px;
    top: 0px;
    background: #fff;
    overflow: visible;    
    z-index: 1200;
    overflow-x: hidden;
    overflow-y: hidden;
    border-radius: 0px 0px 0px 14px;       
    transition: height 0.3s;
}

.menu-perfil-container {
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.161);
}

.menu-perfil-container.collapsed:hover {
    background: #fbf9fb;
    cursor: pointer;
}

.menu-perfil-container.collapsed {
    height: 110px;
    background: transparent;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.161);
    transition: height 0.3s;
}

.menu-perfil-contenido {
    margin-top: 130px;
    margin-left: 40px;
    margin-right: 40px;
    transition: height, top, bottom, 0.3s;
}

.menu-perfil-contenido.collapsed {
    display: none;
    visibility: hidden;
}
